<style>
  @import '//at.alicdn.com/t/font_496303_kqrjhri8l25d0a4i.css';

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  #app {
    display: flex;
    align-items: stretch;
  }

  .some {
    border: 1px solid #ff0000;
  }

  html, body, #app {
