  }

  body {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    font-size: 14px;
    background-color: #eee;
  }

  a {
    text-decoration: none;
    color: #444;
  }

  ul, li {
    list-style: none;
  }

  .btn {
    color: #666;
