<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="main">
          </div>
          <div class="form">

            <h3 @click="showRegister">创建账户</h3>
            <transition name="slide">
              <div v-show="isShowRegister" class="register">
                <input type="text" v-model="register.username" placeholder="用户名">
                <input type="password" v-model="register.password" placeholder="密码">
                <p v-bind:class="{error: register.isError}"> {{register.notice}}</p>
                <div class="button" @click="onRegister">创建账号</div>
              </div>
            </transition>

            <h3 @click="showLogin">登录</h3>
            <transition name="slide">
              <div v-show="isShowLogin" class="login">
                <input type="text" v-model="login.username" placeholder="输入用户名">
                <input type="password" v-model="login.password" placeholder="密码">
                <p v-bind:class="{error: login.isError}"> {{login.notice}}</p>
                <div class="button" @click="onLogin"> 登录</div>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>


</template>

<script>
  import authorise from '@/api/authorise.js'
  authorise.getInfo()
    .then(data => {
      console.log(data)
    })

  export default {
    data() {
      return {
        isShowLogin: true,
        isShowRegister: false,
        login: {
          username: 'hunger',
          password: '123456',
          notice: '输入用户名和密码',
          isError: false
        },
        register: {
          username: '',
          password: '',
          notice: '创建账号后，请记住用户名和密码',
          isError: false
        }
      }
    },

    methods: {
      showLogin() {
        this.isShowLogin = true
        this.isShowRegister = false
      },
      showRegister() {
        this.isShowLogin = false
        this.isShowRegister = true
      },
      checkUserName(username) {
        return /^[\w\u4e00-\u9fa5]{3,15}$/.test(username)
      },
      checkPassword(password) {
        return /^.{6,16}$/.test(password)
        //RegExp.test()用来确认括号内的字符串是否包含符合规则中的值
      },
      onRegister() {
        if (!this.checkUserName(this.register.username)) {

          this.register.isError = true
          this.register.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return
        }
        if (!this.checkPassword(this.register.password)) {
          this.register.isError = true
          this.register.notice = '密码长度为6~16个字符'
          return
        }
        this.register.isError = false
        this.register.notice = ''
        console.log(`start register..., username: ${this.register.username} , password: ${this.register.password}`)
        authorise.register({
          username: this.register.username,
          password: this.register.password
        }).then(res => {
          console.log('register',res)
        })
      },
      onLogin() {
        //默认账户密码hunger 123456
        if (!this.checkUserName(this.login.username)) {
          this.login.isError = true
          this.login.notice = '用户名3~15个字符，仅限于字母数字下划线中文'
          return
        }
        if (!this.checkPassword(this.login.password)) {
          this.login.isError = true
          this.login.notice = '密码长度为6~16个字符'
          return
        }
        this.login.isError = false
        this.login.notice = ''

        console.log(`start login..., username: ${this.login.username} , password: ${this.login.password}`)
        authorise.login({
          username: this.login.username,
          password: this.login.password
        }).then(res => {
            console.log('login',res)
          })
      }

    }

  }

</script>

<style lang="less" scoped>
  /*<style  scoped>*/
  .modal-mask {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 800px;
    height: 500px;
    margin: 0px auto;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    display: flex;

    .main {
      flex: 1;
      background: #36bc64 url(//cloud.hunger-valley.com/17-12-13/38476998.jpg-middle) center center no-repeat;
      background-size: contain;
    }

    .form {
      width: 270px;
      border-left: 1px solid #ccc;
      overflow: hidden;

      h3 {
        padding: 10px 20px;
        margin-top: -1px;
        font-weight: normal;
        font-size: 16px;
        border-top: 1px solid #eee;
        cursor: pointer;

        &:nth-of-type(2) {
          border-bottom: 1px solid #eee;
        }
      }

      .button {
        background-color: #2bb964;
        height: 36px;
        line-height: 36px;
        text-align: center;
        font-weight: bold;
        color: #fff;
        border-radius: 4px;
        margin-top: 18px;
        cursor: pointer;
      }

      .login, .register {
        padding: 0px 20px;
        border-top: 1px solid #eee;
        /*height: 0;*/
        overflow: hidden;
        transition: all .4s;


        input {
          display: block;
          width: 100%;
          height: 35px;
          line-height: 35px;
          padding: 0 6px;
          border-radius: 4px;
          border: 1px solid #ccc;
          outline: none;
          font-size: 14px;
          margin-top: 10px;
        }

        input:focus {
          border: 3px solid #9dcaf8;
        }

        p {
          font-size: 12px;
          margin-top: 10px;
          color: #444;
        }

        .error {
          color: red;
        }
      }

      .slide-enter-active, .slide-leave-active {
        height: 170px;
      }

      .slide-enter, .slide-leave-to {

        height: 0px;
      }

    }
  }


</style>




